body,html{font-size:16px}#desktop-menu,.hamburger{margin-top:1px}.hamburger{height:30px;width:30px}.hamburger>.bar-inner{-moz-transition:.2s ease all;-ms-transition:.2s ease all;-o-transition:.2s ease all;-webkit-transition:all .2s ease;background-color:#303139;float:right;height:6px;transition:all .2s ease}.hamburger>.bar-inner.upper{width:50%}.hamburger>.bar-inner.mid{margin:6px 0;width:110%}.hamburger>.bar-inner.lower{width:80%}.hamburger:hover>.bar-inner{background-color:#f7f7f7;width:100%}.disabled-link{pointer-events:none}